export default {
	data() {
		return {
			zhanghao: '',
			mima: '',
		};
	},
	methods: {
		denglu: function() {
			var post_shoujihao = this.zhanghao;
			var post_mima = this.mima;
			var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
			if (post_shoujihao == '') {
				uni.showModal({
					title: '提示',
					content: '请输入手机号',
					showCancel: false
				});
			} else {
				if (!myreg.test(post_shoujihao)) {
					uni.showModal({
						title: '提示',
						content: '请输入正确手机号',
						showCancel: false
					});
				} else {
					if (post_mima == '') {
						uni.showModal({
							title: '提示',
							content: '请输入密码',
							showCancel: false
						});
					} else {
						this.post_denglu();
					}
				}
			}

		},
		post_denglu: function() {
			console.log('登录');

			var fuwuqi = getApp().globalData.quanju_fuwuqi_url;
			var shoujihao = this.zhanghao;
			var mima = this.mima;
			var _this = this;
			uni.request({
				url: fuwuqi + 'denglu_caozuo.php', //仅为示例，并非真实接口地址。
				method: 'POST',
				data: {
					shoujihao: shoujihao,
					mima: mima
				},
				header: {
					//'custom-header': 'hello' //自定义请求头信息
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					switch (res.data.zhuangtai) {
						case '1':
							getApp().globalData.quanju_huiyuan_id = shoujihao;
							getApp().globalData.quanju_huiyuan_mima = mima;
							getApp().globalData.quanju_huiyuan_mingcheng = res.data.xiangqing;
							uni.redirectTo({
								url:'../shouye/shouye'
							})
							break;
						case '2':
							uni.showModal({
								title: '提示',
								content: '账号或密码错误',
								showCancel: false
							});

							break;
						case '3':
							uni.showModal({
								title: '提示',
								content: '登录失败',
								showCancel: false
							});

							break;
						default:
							uni.showModal({
								title: '错误',
								content: res.data.xiangqing,
								showCancel: false
							});
							break;
					}
				},
				fail() {
					uni.showModal({
						title: '错误',
						content: '请求服务器错误',
						showCancel: false
					});
				}
			});
		}
	},
	onLoad: function() {

	}
};
